<template>
    <div class="about">
        <h1>{{id ? '编辑' : '新建'}}分类</h1>
        <el-form label-width="120px" @submit.native.prevent="save">
          <el-form-item label="上级分类">
                <el-select v-model="model.parent">
                  <el-option v-for="item in parents" :key="item._id"
                  :label="item.name" :value="item._id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="名称">
                <el-input v-model="model.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {},
      parents: []
    }
  },
  methods: {
    // 做一个条件判断
    async save () {
      let res
      if(this.id){
      const res = await this.$http.put(`rest/categories/${this.id}`,this.model)
      }else {
      const res = await this.$http.post('rest/categories',this.model)
      }
      this.$router.push('/categories/list') //直接跳转至分类列表
      this.$message({
        type:'success',
        message:'保存成功'
            })
        },
      async fetch(){
        const res = await this.$http.get(`rest/categories/${this.id}`)
        this.model = res.data
        },
        async fetchParents(){
        const res = await this.$http.get(`rest/categories`)
        this.parents = res.data
        }
      },

      created(){
        this.fetchParents() // 获取下拉框里的数据
        this.id && this.fetch() // 同时满足两个条件 点击编辑按钮以后 输入框里面拿到内容
      }
}
</script>
