<template>
    <div class="about">
        <h1>{{id ? '编辑' : '新建'}}管理员</h1>
        <el-form label-width="120px" @submit.native.prevent="save">
            <el-form-item label="用户名">
                <el-input v-model="model.username"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input type="text" v-model="model.password"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {},
    }
  },
  methods: {
    // 做一个条件判断
    async save () {
      let res
      if(this.id){
      const res = await this.$http.put(`rest/admin_users/${this.id}`,this.model)
      }else {
      const res = await this.$http.post('rest/admin_users',this.model)
      }
      this.$router.push('/admin_users/list') //直接跳转至分类列表
      this.$message({
        type:'success',
        message:'保存成功'
            })
        },
      async fetch(){
        const res = await this.$http.get(`rest/admin_users/${this.id}`)
        this.model = res.data
        },
      },

      created(){
        this.id && this.fetch() // 同时满足两个条件 点击编辑按钮以后 输入框里面拿到内容
      }
}
</script>
