<template>
    <div class="about">
        <h1>{{id ? '编辑' : '新建'}}文章</h1>
        <el-form label-width="120px" @submit.native.prevent="save">
          <el-form-item label="所属分类">
                <el-select v-model="model.categories" multiple>
                  <el-option v-for="item in categories" :key="item._id"
                  :label="item.name" :value="item._id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="标题">
                <el-input v-model="model.title"></el-input>
            </el-form-item>
            <el-form-item label="详情">
              <vue-editor v-model="model.body" useCustomImageHandler @image-added="handleImageAdded"></vue-editor>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import { VueEditor } from "vue2-editor";
export default {
  props: {
    id: {}
  },
  components: {
    VueEditor
  },
  data () {
    return {
      model: {},
      categories: []
    }
  },
  methods: {
    // 富文本编辑器自定义图片上传的方式 转换为base64会导致文件太大 网页响应慢
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData();
      formData.append("file", file);
      const res = await this.$http.post('upload', formData);
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    // 做一个条件判断
    async save () {
      let res
      if(this.id){
      const res = await this.$http.put(`rest/articles/${this.id}`,this.model)
      }else {
      const res = await this.$http.post('rest/articles',this.model)
      }
      this.$router.push('/articles/list') //直接跳转至分类列表
      this.$message({
        type:'success',
        message:'保存成功'
            })
        },
      async fetch(){
        const res = await this.$http.get(`rest/articles/${this.id}`)
        this.model = res.data
        },
        async fetchCategories(){
        const res = await this.$http.get(`rest/categories`)
        this.categories = res.data;
        }
      },

      created(){
        this.fetchCategories() // 获取下拉框里的数据
        this.id && this.fetch() // 同时满足两个条件 点击编辑按钮以后 输入框里面拿到内容
      }
};
</script>
